.TodoItem {
  background-color: #009ffd;
  color: #fafafa;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 3px 9px rgba(32, 35, 41, 0.5);
  border-radius: 25px;
}

.TodoItem--completed {
  background-color: rgba(0, 160, 253, 0.4);
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}

.TodoItem-p--completed {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}
.Icon-check {
  position: absolute;
  left: 12px;
  cursor: pointer;
}

.Icon-check--active {
  color: hsl(122, 100%, 54%);
}

.Icon-delete {
  position: absolute;
  top: -5px;
  right: 0;
}
.Icon-delete:hover {
  color: red;
}
